"use client";

import { useEffect, useRef, useState } from 'react';
import { Card } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { BarChart3, TrendingUp, Activity, Maximize2 } from 'lucide-react';

interface TradingChartProps {
  symbol: string;
  height?: number;
  showVolume?: boolean;
}

export default function TradingChart({ symbol, height = 400, showVolume = true }: TradingChartProps) {
  const chartContainerRef = useRef<HTMLDivElement>(null);
  const chartRef = useRef<any>(null);
  const candlestickSeriesRef = useRef<any>(null);
  const volumeSeriesRef = useRef<any>(null);

  const [interval, setInterval] = useState<string>('1D');
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    if (!chartContainerRef.current) return;

    // Dynamically import lightweight-charts to avoid SSR issues
    import('lightweight-charts').then(({ createChart }) => {
      if (!chartContainerRef.current) return;

      // Create chart
      const chart = createChart(chartContainerRef.current, {
        layout: {
          background: { color: 'transparent' },
          textColor: '#D9D9D9',
        },
        grid: {
          vertLines: { color: 'rgba(197, 203, 206, 0.1)' },
          horzLines: { color: 'rgba(197, 203, 206, 0.1)' },
        },
        width: chartContainerRef.current.clientWidth,
        height: height,
        timeScale: {
          timeVisible: true,
          secondsVisible: false,
        },
        crosshair: {
          mode: 1,
        },
        rightPriceScale: {
          borderVisible: false,
        },
        leftPriceScale: {
          visible: showVolume,
          borderVisible: false,
        },
      });

      chartRef.current = chart;

      // Add candlestick series
      const candlestickSeries = chart.addCandlestickSeries({
        upColor: '#26a69a',
        downColor: '#ef5350',
        borderVisible: false,
        wickUpColor: '#26a69a',
        wickDownColor: '#ef5350',
      });

      candlestickSeriesRef.current = candlestickSeries;

      // Add volume series if enabled
      if (showVolume) {
        const volumeSeries = chart.addHistogramSeries({
          color: '#26a69a',
          priceFormat: {
            type: 'volume',
          },
          priceScaleId: 'left',
        });

        volumeSeriesRef.current = volumeSeries;
      }

      // Handle resize
      const handleResize = () => {
        if (chartContainerRef.current) {
          chart.applyOptions({
            width: chartContainerRef.current.clientWidth,
          });
        }
      };

      window.addEventListener('resize', handleResize);

      // Return cleanup function
      return () => {
        window.removeEventListener('resize', handleResize);
        chart.remove();
      };
    }).catch(error => {
      console.error('Error loading chart library:', error);
    });
  }, [height, showVolume]);

  // Fetch and update chart data when symbol or interval changes
  useEffect(() => {
    if (!symbol) return;

    fetchChartData(symbol, interval);
  }, [symbol, interval]);

  const fetchChartData = async (sym: string, int: string) => {
    setLoading(true);
    try {
      // Map interval to API format
      const intervalMap: Record<string, string> = {
        '1M': '1m',
        '5M': '5m',
        '15M': '15m',
        '1H': '1h',
        '1D': '1d'
      };

      const apiInterval = intervalMap[int] || '1d';
      const period = int === '1D' ? '1y' : int === '1H' ? '1mo' : '5d';

      const response = await fetch(`/api/stocks/historical/${sym}/${period}?interval=${apiInterval}`);

      if (!response.ok) {
        console.error('Failed to fetch chart data');
        return;
      }

      const data = await response.json();

      if (!data || data.length === 0) {
        console.error('No data received');
        return;
      }

      // Transform data to Lightweight Charts format
      const candlestickData = data.map((bar: any) => ({
        time: (new Date(bar.date).getTime() / 1000) as any,
        open: bar.open,
        high: bar.high,
        low: bar.low,
        close: bar.close,
      }));

      const volumeData = data.map((bar: any) => ({
        time: (new Date(bar.date).getTime() / 1000) as any,
        value: bar.volume,
        color: bar.close >= bar.open ? 'rgba(38, 166, 154, 0.5)' : 'rgba(239, 83, 80, 0.5)',
      }));

      // Update series
      if (candlestickSeriesRef.current) {
        candlestickSeriesRef.current.setData(candlestickData);
      }

      if (volumeSeriesRef.current && showVolume) {
        volumeSeriesRef.current.setData(volumeData);
      }

      // Fit content
      if (chartRef.current) {
        chartRef.current.timeScale().fitContent();
      }

    } catch (error) {
      console.error('Error fetching chart data:', error);
    } finally {
      setLoading(false);
    }
  };

  const intervals = ['1M', '5M', '15M', '1H', '1D'];

  return (
    <Card className="p-4">
      {/* Chart Header */}
      <div className="flex items-center justify-between mb-4">
        <div className="flex items-center gap-2">
          <BarChart3 className="h-5 w-5 text-blue-500" />
          <h3 className="font-semibold">{symbol}</h3>
          {loading && (
            <div className="animate-spin h-4 w-4 border-2 border-blue-500 border-t-transparent rounded-full" />
          )}
        </div>

        {/* Interval Selector */}
        <div className="flex items-center gap-1">
          {intervals.map((int) => (
            <Button
              key={int}
              variant={interval === int ? "default" : "outline"}
              size="sm"
              onClick={() => setInterval(int)}
              className="h-8 px-3 text-xs"
            >
              {int}
            </Button>
          ))}
        </div>
      </div>

      {/* Chart Container */}
      <div
        ref={chartContainerRef}
        className="relative rounded-lg overflow-hidden bg-gray-50 dark:bg-gray-900"
        style={{ height: `${height}px` }}
      />

      {/* Chart Footer - Stats */}
      <div className="mt-4 grid grid-cols-4 gap-4 text-sm">
        <div>
          <span className="text-muted-foreground">O:</span>
          <span className="ml-1 font-medium">--</span>
        </div>
        <div>
          <span className="text-muted-foreground">H:</span>
          <span className="ml-1 font-medium text-green-500">--</span>
        </div>
        <div>
          <span className="text-muted-foreground">L:</span>
          <span className="ml-1 font-medium text-red-500">--</span>
        </div>
        <div>
          <span className="text-muted-foreground">C:</span>
          <span className="ml-1 font-medium">--</span>
        </div>
      </div>
    </Card>
  );
}
